import axios from 'axios'
import { SET_TOKEN, REGISTER_ERROR, LOGIN_ERROR } from '../types/AuthTypes.js'
import { SET_LOADER, CLOSE_LOADER, REMOVE_ERROR } from '../types/CommonTypes.js'
import { BASE_URL } from '../../config'

const token = localStorage.getItem('token')
export const register = (state) => {
    return async (dispatch) => {
        const config = {
            headers: {
                'Content-Type': 'application/json',
            },
        }
        dispatch({ type: SET_LOADER })
        try {
            const res = await axios.post(
                `${BASE_URL}/auth/register`,
                state,
                config
            )
            dispatch({ type: CLOSE_LOADER })
            console.log(res)
            if (res.status === 200) {
                window.location.href = `/auth/login`
            }
        } catch (error) {
            dispatch({ type: CLOSE_LOADER })
            dispatch({
                type: REGISTER_ERROR,
                payload: error.response.data.message,
            })
            dispatch({ type: REMOVE_ERROR })
        }
    }
}
export const login = (state) => {
    return async (dispatch) => {
        const config = {
            headers: {
                'Content-Type': 'application/json',
            },
        }
        dispatch({ type: SET_LOADER })
        try {
            const { data } = await axios.post(
                `${BASE_URL}/auth/login`,
                state,
                config
            )
            dispatch({ type: CLOSE_LOADER })
            localStorage.setItem('token', data.token)
            dispatch({ type: SET_TOKEN, payload: data.token })
        } catch (error) {
            dispatch({ type: CLOSE_LOADER })
            console.log('error', error.response.data.message)
            dispatch({
                type: LOGIN_ERROR,
                payload: error.response.data.message,
            })
            dispatch({ type: REMOVE_ERROR })
        }
    }
}

export const changePassword = async (data) => {
    const config = {
        headers: {
            "Authorization": `${token}`
        }
    }
    await axios.post(`${BASE_URL}/auth/change-password`, data, config).then((response) => {
        window.location.href = '/profile';
    }).catch((error) => {
        throw error.response.data.message
    })
}