import { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import GoBack from '../../../components/Shared/GoBack'
import { Button, Form, Input, Select, Upload, Tag } from 'antd'
import toast, { Toaster } from 'react-hot-toast'
import { UploadOutlined, PlusOutlined } from '@ant-design/icons'
import { getAllCategories } from '../../../store/asyncMethod/CategoryMethod'
import { createPost } from '../../../store/asyncMethod/PostMethod'
import { BASE_URL } from '../../../config'
import QuillEditor from './QuillEditor'
import slugNpm from 'slug'

import { Helmet } from 'react-helmet'

const Create = (props) => {
    const history = useHistory()
    const dispatch = useDispatch()
    const [form] = Form.useForm()

    const { user, loading } = useSelector((state) => state.AuthReducer)
    const { createPostError, redirect } = useSelector(
        (state) => state.PostReducer
    )
    const { categories } = useSelector((state) => state.CategoryReducer)
    const [tagState, setTagState] = useState({
        tags: ['nodejs', 'reactjs'],
        inputVisible: false,
        inputValue: '',
    })
    const [value, setValue] = useState('')
    const [content, setContent] = useState('')
    const [files, setFiles] = useState([])

    useEffect(() => {
        dispatch(getAllCategories(1, 20))
    }, [])

    useEffect(() => {
        if (redirect) {
            props.history.push('/admin/posts')
        }
        if (createPostError) {
            toast.error(createPostError)
        }
    }, [createPostError, redirect])

    const uploadImagePost = (info) => {
        return `${BASE_URL}/common/upload-file`
    }

    const handleClose = (removedTag) => {
        const tags = tagState.tags.filter((tag) => tag !== removedTag)
        setTagState({ tags })
    }
    const forMap = (tag) => {
        const tagElem = (
            <Tag
                closable
                onClose={(e) => {
                    e.preventDefault()
                    handleClose(tag)
                }}
            >
                {tag}
            </Tag>
        )
        return (
            <span key={tag} style={{ display: 'inline-block' }}>
                {tagElem}
            </span>
        )
    }
    const showInput = () => {
        setTagState({ ...tagState, inputVisible: true })
    }
    const handleInputChange = (e) => {
        setTagState({ ...tagState, inputValue: e.target.value })
    }

    const handleInputConfirm = () => {
        const { inputValue } = tagState
        let { tags } = tagState
        if (inputValue && tags.indexOf(inputValue) === -1) {
            tags = [...tags, inputValue]
        }
        setTagState({
            tags,
            inputVisible: false,
            inputValue: '',
        })
    }

    const { tags, inputVisible, inputValue } = tagState

    form.setFieldsValue({ tags })

    const tagChild = tags.map(forMap)

    const handleTitleChange = (e) => {
        const slug = slugNpm(e.target.value)

        form.setFieldsValue({ slug })
    }

    const onEditorChange = (value) => {
        form.setFieldsValue({ content: value })
    }

    const onFilesChange = (files) => {
        setFiles(files)
    }
    const onFinish = (values) => {
        values.userId = user._id
        values.image = values.image.file.response.data.url
        dispatch(createPost(values))
    }

    const onFinishFailed = (errorInfo) => {
        console.log(errorInfo)
    }
    const onReset = () => {
        form.resetFields()
    }

    return (
        <>
            <div className="post">
                <GoBack />
                <Helmet>
                    <title>Create new post</title>
                    <meta name="description" content="Create new post" />
                </Helmet>
                <div className="">
                    <Toaster
                        position="top-right"
                        reverseOrder={false}
                        gutter={8}
                        toastOptions={{
                            duration: 1000,
                            style: {
                                background: '#363636',
                                color: '#fff',
                                fontSize: 13,
                            },
                        }}
                    />
                    <div className="container">
                        <div className="form__data">
                            <h3 className="form__data__text">
                                Create new post
                            </h3>
                            <Form
                                form={form}
                                name="basic"
                                labelCol={{
                                    span: 2,
                                }}
                                wrapperCol={{
                                    span: 20,
                                }}
                                initialValues={{
                                    remember: true,
                                }}
                                onFinish={onFinish}
                                onFinishFailed={onFinishFailed}
                                autoComplete="off"
                            >
                                <Form.Item
                                    label="Title"
                                    name="title"
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input your title!',
                                        },
                                    ]}
                                >
                                    <Input onChange={handleTitleChange} />
                                </Form.Item>

                                <Form.Item
                                    label="Image"
                                    name="image"
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input your image!',
                                        },
                                    ]}
                                >
                                    <Upload
                                        action={uploadImagePost}
                                        listType="picture"
                                        maxCount={1}
                                    >
                                        <Button icon={<UploadOutlined />}>
                                            Click to upload
                                        </Button>
                                    </Upload>
                                </Form.Item>
                                <Form.Item
                                    label="Description"
                                    name="description"
                                    rules={[
                                        {
                                            required: true,
                                            message:
                                                'Please input your description!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                                <Form.Item
                                    label="Slug"
                                    name="slug"
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input your slug!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                                <Form.Item
                                    label="Content"
                                    name="content"
                                    rules={[
                                        {
                                            required: true,
                                            message:
                                                'Please input your content!',
                                        },
                                    ]}
                                >
                                    <QuillEditor
                                        onEditorChange={onEditorChange}
                                        onFilesChange={onFilesChange}
                                    />
                                </Form.Item>

                                <Form.Item
                                    label="Tag"
                                    name="tags"
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input your tags!',
                                        },
                                    ]}
                                >
                                    {tagChild}
                                    {inputVisible && (
                                        <Input
                                            type="text"
                                            size="small"
                                            style={{ width: 78 }}
                                            value={inputValue}
                                            onChange={handleInputChange}
                                            onBlur={handleInputConfirm}
                                            onPressEnter={handleInputConfirm}
                                        />
                                    )}
                                    {!inputVisible && (
                                        <Tag onClick={showInput}>
                                            <PlusOutlined /> New Tag
                                        </Tag>
                                    )}
                                </Form.Item>
                                <Form.Item
                                    label="Category"
                                    name="categoryId"
                                    rules={[
                                        {
                                            required: true,
                                            message:
                                                'Please input your category!',
                                        },
                                    ]}
                                >
                                    <Select
                                        placeholder="Select a category and change "
                                        allowClear
                                    >
                                        {categories.map((category) => (
                                            <Select.Option
                                                key={category.id}
                                                value={category.id}
                                            >
                                                {category.name}
                                            </Select.Option>
                                        ))}
                                    </Select>
                                </Form.Item>
                                <Form.Item
                                    wrapperCol={{
                                        offset: 2,
                                        span: 20,
                                    }}
                                >
                                    <Button type="button" onClick={onReset}>
                                        Cancel
                                    </Button>
                                    <Button
                                        type="primary"
                                        htmlType="submit"
                                        style={{ marginLeft: 15 }}
                                    >
                                        Create
                                    </Button>
                                </Form.Item>
                            </Form>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Create
