import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { Button, Form, Input, Select, Upload, Tag, Image } from 'antd'
import toast, { Toaster } from 'react-hot-toast'
import _ from 'lodash'
import GoBack from '../../../components/Shared/GoBack'
import { getPostDetail, editPost } from '../../../store/asyncMethod/PostMethod'
import { UploadOutlined, PlusOutlined } from '@ant-design/icons'
import { POST_RESET } from '../../../store/types/PostTypes.js'
import QuillEditor from './QuillEditor'
import { Helmet } from 'react-helmet'
import { REDIRECT_FALSE } from '../../../store/types/CommonTypes.js'
import { getAllCategories } from '../../../store/asyncMethod/CategoryMethod'
import { BASE_URL } from '../../../config'

const Edit = (props) => {
    const { id } = props.match.params
    const history = useHistory()
    const dispatch = useDispatch()
    const [form] = Form.useForm()
    const { categories } = useSelector((state) => state.CategoryReducer)
    const { post, postStatus, editPostError, redirect } = useSelector(
        (state) => state.PostReducer
    )
    const [urlImage, setUrlImage] = useState(post?.image)
    const [tagState, setTagState] = useState({
        tags: !_.isEmpty(post.tags) ? post.tags : [],
        inputVisible: false,
        inputValue: '',
    })
    const { tags, inputVisible, inputValue } = tagState

    const uploadImagePost = (info) => {
        return `${BASE_URL}/common/upload-file`
    }
    useEffect(() => {
        if (postStatus) {
            form.setFieldsValue({
                title: post.title,
                description: post.description,
                slug: post.slug,
                image: post.image,
                content: post.content,
                categoryId: post.categoryId._id,
                tags: post.tags,
            })
            setUrlImage(post.image)
            dispatch({ type: POST_RESET })
        } else {
            dispatch(getPostDetail(id))
        }
    }, [post])

    useEffect(() => {
        dispatch(getAllCategories(1, 20))
    }, [])

    const forMap = (tag) => {
        const tagElem = (
            <Tag
                closable
                onClose={(e) => {
                    e.preventDefault()
                    handleClose(tag)
                }}
            >
                {tag}
            </Tag>
        )
        return (
            <span key={tag} style={{ display: 'inline-block' }}>
                {tagElem}
            </span>
        )
    }

    useEffect(() => {
        if (!_.isEmpty(post.tags)) {
            setTagState({
                tags: post.tags,
                inputVisible: false,
                inputValue: '',
            })
        }
    }, [post])

    const handleClose = (removedTag) => {
        const tags = tagState.tags.filter((tag) => tag !== removedTag)
        setTagState({ tags })
    }

    const tagChild = tags?.map(forMap)
    const showInput = () => {
        setTagState({ ...tagState, inputVisible: true })
    }
    const handleInputChange = (e) => {
        setTagState({ ...tagState, inputValue: e.target.value })
    }

    const handleInputConfirm = () => {
        const { inputValue } = tagState
        let { tags } = tagState
        if (inputValue && tags.indexOf(inputValue) === -1) {
            tags = [...tags, inputValue]
        }
        setTagState({
            tags,
            inputVisible: false,
            inputValue: '',
        })
    }

    useEffect(() => {
        if (redirect) {
            dispatch({ type: REDIRECT_FALSE })
            props.history.push('/admin/posts')
        }
    }, [redirect])

    useEffect(() => {
        if (editPostError) {
            toast.error(editPostError)
        }
    }, [editPostError])

    const handleUpload = (info) => {
        if (info.file.status === 'done') {
            setUrlImage(info.file.response.data.url)
            console.log(
                ' ~ file: edit.jsx ~ line 139 ~ handleUpload ~ setUrlImage',
                setUrlImage
            )
            // form.setFieldsValue('image', info.file.response.data.url)
            form.setFieldsValue('image', urlImage)
        } else if (info.file.status === 'error') {
            toast.error(`${info.file.name} file upload failed.`)
        }
    }

    const onFinish = (values) => {
        values.image = urlImage
        dispatch(editPost(id, values))
    }

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo)
    }
    const onReset = () => {
        form.resetFields()
    }

    const onEditorChange = (value) => {
        // console.log(value)
        form.setFieldsValue({ content: value })
    }

    const onFilesChange = (files) => {
        // eslint-disable-next-line no-undef
        setFiles(files)
    }

    return (
        <div>
            <GoBack />
            <Helmet>
                <title>Edit post</title>
                <meta name="description" content="Edit post" />
            </Helmet>
            <div>
                <Toaster
                    position="top-right"
                    reverseOrder={false}
                    gutter={8}
                    toastOptions={{
                        duration: 1000,
                        style: {
                            background: '#363636',
                            color: '#fff',
                            fontSize: 13,
                        },
                    }}
                />
                <div className="container">
                    <div className="form__data">
                        <h3 className="form__data__text">Edit post</h3>
                        <Form
                            form={form}
                            name="basic"
                            className="formCreatePost"
                            labelCol={{
                                span: 2,
                            }}
                            wrapperCol={{
                                span: 20,
                            }}
                            initialValues={{
                                remember: true,
                            }}
                            onFinish={onFinish}
                            onFinishFailed={onFinishFailed}
                            autoComplete="off"
                        >
                            <Form.Item
                                label="Title"
                                name="title"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please input your title!',
                                    },
                                ]}
                            >
                                <Input />
                            </Form.Item>

                            <Form.Item
                                label="Image"
                                name="image"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please input your image!',
                                    },
                                ]}
                            >
                                <Image width={200} src={urlImage} />
                                <Upload
                                    action={uploadImagePost}
                                    onChange={handleUpload}
                                    listType="picture"
                                    maxCount={1}
                                >
                                    <Button
                                        style={{
                                            marginTop: '2rem',
                                        }}
                                        icon={<UploadOutlined />}
                                    >
                                        Click to upload
                                    </Button>
                                </Upload>
                            </Form.Item>
                            <Form.Item
                                label="Description"
                                name="description"
                                rules={[
                                    {
                                        required: true,
                                        message:
                                            'Please input your description!',
                                    },
                                ]}
                            >
                                <Input />
                            </Form.Item>
                            <Form.Item
                                label="Slug"
                                name="slug"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please input your slug!',
                                    },
                                ]}
                            >
                                <Input />
                            </Form.Item>
                            <Form.Item
                                label="Content"
                                name="content"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please input your content!',
                                    },
                                ]}
                            >
                                <QuillEditor
                                    value={post.content}
                                    onEditorChange={onEditorChange}
                                    onFilesChange={onFilesChange}
                                />
                            </Form.Item>

                            {
                                <Form.Item
                                    label="Tag"
                                    name="tags"
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input your tags!',
                                        },
                                    ]}
                                >
                                    {tagChild}
                                    {inputVisible && (
                                        <Input
                                            type="text"
                                            size="small"
                                            style={{ width: 78 }}
                                            value={inputValue}
                                            onChange={handleInputChange}
                                            onBlur={handleInputConfirm}
                                            onPressEnter={handleInputConfirm}
                                        />
                                    )}
                                    {!inputVisible && (
                                        <Tag onClick={showInput}>
                                            <PlusOutlined /> New Tag
                                        </Tag>
                                    )}
                                </Form.Item>
                            }
                            <Form.Item
                                label="Category"
                                name="categoryId"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Please input your category!',
                                    },
                                ]}
                            >
                                <Select
                                    placeholder="Select a category and change "
                                    allowClear
                                >
                                    {categories.map((category) => (
                                        <Select.Option
                                            key={category.id}
                                            value={category.id}
                                        >
                                            {category.name}
                                        </Select.Option>
                                    ))}
                                </Select>
                            </Form.Item>
                            <Form.Item
                                wrapperCol={{
                                    offset: 2,
                                    span: 20,
                                }}
                            >
                                <Button type="button" onClick={onReset}>
                                    Cancel
                                </Button>
                                <Button
                                    type="primary"
                                    htmlType="submit"
                                    style={{ marginLeft: 15 }}
                                >
                                    Edit
                                </Button>
                            </Form.Item>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Edit
